@use 'src/styles/vars';
@import 'src/styles/mixins';


.container {
    width: 75%;
    height: 100%;
    margin: auto;
    display: flex;  
    gap: 3em;

    @include init {
        flex-direction: column;
        gap: 3em;
    }

    @include mobile {
        flex-direction: row;
        justify-content: space-evenly; 
    }

    &__box {
        border-radius: .5em;
        background-color: vars.$bg-box;

        @include init {
            width: 100%;
            padding-bottom: 5em;
        }

        @include mobile {
            width: calc(100% / 3);
        }

        @include desktop {
            overflow-x: hidden;
            overflow-y: hidden;
        }

        .bg {
            width: 100%;
            height: 40%;
            display: flex;
            justify-content: center;
            align-items: center;
            border-top-left-radius: .5em;
            border-top-right-radius: .5em;
            background-image: url('../../assets/images/Rectangle.png');
            background-size: cover;
            background-repeat: no-repeat;
        }

        .content {     
            &__icon_wrapper {
                display: flex;
                justify-content: flex-start;
                width: 85%;
                margin-left: auto;
                margin-right: auto;
                margin-top: 3em;
            }

            &__text_wrapper {
                text-align: start;
                width: 85%;
                margin-left: auto;
                margin-right: auto;

                @keyframes h2-animation {
                    from {
                        transform: translateX(-150%);
                    }
                    to {
                        transform: translateX(0);
                    }
                }

                h2 {
                    font-size: 1.5em;
                    
                    animation-name: h2-animation;     
                    animation-duration: 1s;
                    animation-fill-mode: forwards;
                }

                p {
                    margin-top: -1em;
                }

                button {
                    border: 0;
                    font-size: 1em;
                    font-weight: bold;
                    color: vars.$word-important;
                    background-color: vars.$bg-box;
                    padding-left: 0;

                    img {
                        margin-left: .3em;
                    }

                }

                @keyframes button-hover-animation {
                    to {
                        scale: 1.1;
                    }
                }

                button:hover {
                    animation-name: button-hover-animation;
                    animation-duration: .5s;
                    animation-fill-mode: forwards;
                    cursor: pointer;
                }
            }
        }
    }
}
